@charset "UTF-8";
:root {
  --font-primary: "Raleway", sans-serif;
  --font-secondary: "Open Sans", sans-serif;
}

:root {
  --dark-intro: hsl(217, 28%, 15%);
  --dark-main: hsl(218, 28%, 13%);
  --dark-footer: hsl(216, 53%, 9%);
  --dark-test: hsl(219, 30%, 18%);
  --cyan-grad: hsl(176, 68%, 64%);
  --blue-grad: hsl(198, 60%, 50%);
  --light-red: hsl(0, 100%, 63%);
  --clr-white: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.3;
  font-weight: normal;
  font-family: var(--font-primary), var(--font-secondary);
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  font-size: 0.875rem;
}
@media only screen and (min-width: 40em) {
  body {
    font-size: 0.9375rem;
  }
}
@media only screen and (min-width: 64em) {
  body {
    font-size: 1rem;
  }
}

li {
  list-style: none;
}

ul {
  padding: 0;
  margin: 0;
}

/*remove blue background when clicking*/
input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/*img*/
img,
picture {
  display: block;
  max-width: 100%;
}

/*Flex*/
.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-fd-c {
  flex-direction: column;
}

.none {
  display: none;
}

/*Wrapper - Content General*/
.u-wrapper {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.mb-paragraph {
  margin-bottom: 2rem;
}

.pd-tb-default {
  padding-top: 1.5rem;
  padding-bottom: 8rem;
}

/*Titles, Subtitles and copy (paragraph)*/
:is(.u-title, .u-subtitle) {
  font-weight: bold;
  margin-bottom: 1rem;
}

.u-title {
  font-size: 1.7em;
  line-height: 1.6;
}
@media only screen and (min-width: 64em) {
  .u-title {
    font-size: 2.1em;
  }
}

.u-subtitle {
  font-size: 1.3em;
}
@media only screen and (min-width: 64em) {
  .u-subtitle {
    font-size: 1.5em;
  }
}

.u-copy {
  line-height: 1.5;
  font-family: var(--font-secondary);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.btn {
  min-width: 15rem;
  border-radius: 100vw;
  border: none;
  outline: none;
  padding: 1rem;
  display: inline-block;
  cursor: pointer;
  font-weight: bold;
  transform: scale(1);
  border: 1px solid transparent;
  transition: transform 200ms ease, background 200ms ease;
  font-family: var(--font-primary);
}
@media only screen and (min-width: 64em) {
  .btn {
    min-width: 13.125rem;
    font-size: 1rem;
  }
}
.btn:hover, .btn:focus {
  transform: scale(1.01) translateY(-0.125em);
}
.btn-primary {
  color: var(--clr-white);
  background: linear-gradient(127deg, #65e2d9 -25%, #339ecc 61%);
}
.btn-primary:hover, .btn-primary:focus {
  background: #8edae2;
}

.main-header {
  background-color: var(--dark-intro);
}

.navigation {
  padding: 1.5rem;
  max-width: 90em;
  margin-left: auto;
  margin-right: auto;
}
@media only screen and (min-width: 40em) {
  .navigation {
    padding: 4rem 2.25rem 1rem;
  }
}
.navigation .logo {
  width: 70%;
}

.menu-item:not(:last-child) {
  margin-right: 1.5rem;
}
.menu-link {
  display: inline-block;
  width: max-content;
  color: var(--clr-white);
  border-bottom: 1px solid transparent;
  transition: border-bottom 200ms ease-in, font-weight 200ms ease-in;
}
.menu-link:hover, .menu-link:focus {
  border-bottom: 1px solid var(--clr-white);
  font-weight: bold;
}

.hero {
  background: url(../../images/bg-curvy-mobile.svg) center no-repeat, linear-gradient(to bottom, var(--dark-intro) 58%, var(--dark-main) 58%, var(--dark-main) 100%) no-repeat;
  background-size: 100% 11.25rem, 100%;
  width: 100%;
  text-align: center;
  color: var(--clr-white);
  position: relative;
}
@media only screen and (min-width: 40em) {
  .hero {
    background: url(../../images/bg-curvy-desktop.svg) bottom no-repeat, linear-gradient(var(--dark-intro), var(--dark-intro));
    background-size: 100% 25rem, 100%;
  }
}
.hero-content {
  min-width: 19.0625rem;
  max-width: 31.5rem;
  margin: 0 auto;
}
.hero-img {
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
}
.hero-img img {
  margin: 0 auto;
}

main {
  background-color: var(--dark-main);
  color: var(--clr-white);
}

/*Features*/
.features {
  display: grid;
  grid-template-columns: 1fr;
  gap: 5rem;
  text-align: center;
  align-items: center;
  max-width: 90em;
  margin-left: auto;
  margin-right: auto;
}
@media only screen and (min-width: 40em) {
  .features {
    gap: 3rem;
    padding-left: 2rem;
    padding-right: 2rem;
  }
}
@media only screen and (min-width: 64em) {
  .features {
    gap: 4.5rem;
    padding-left: 4rem;
    padding-right: 4rem;
  }
}
@media only screen and (min-width: 40em) {
  .features {
    grid-template-columns: repeat(2, 1fr);
  }
}
.features-item img {
  margin: auto;
  margin-bottom: 2rem;
}

/*Team*/
.team {
  max-width: 90em;
  margin-left: auto;
  margin-right: auto;
  flex-direction: row;
  gap: 4rem;
}
@media only screen and (min-width: 40em) {
  .team {
    gap: 3rem;
    padding-left: 2rem;
    padding-right: 2rem;
  }
}
@media only screen and (min-width: 64em) {
  .team {
    gap: 4.5rem;
    padding-left: 4rem;
    padding-right: 4rem;
  }
}
.team-img {
  margin-bottom: 2rem;
}
.team-img img {
  margin: 0 auto;
}
.team-content > h3 {
  max-width: 18.75rem;
  line-height: 1.3;
}
.team-content > p {
  margin-bottom: 1.5rem;
  max-width: 34.375rem;
}
.team-link {
  color: var(--cyan-grad);
  padding-bottom: 0.375rem;
  border-bottom: 1px solid var(--cyan-grad);
  transition: color 200ms ease-in-out, border-bottom 200ms ease-in-out;
}
.team-link:hover, .team-link:focus {
  color: var(--clr-white);
  border-bottom: 1px solid var(--clr-white);
}
.team-link::after {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "ïš";
  margin-left: 0.5rem;
}

/*Testimonials*/
.testimonials {
  max-width: 90em;
  margin-left: auto;
  margin-right: auto;
}
.testimonials-content {
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  gap: 1.5rem;
  position: relative;
  z-index: 1;
}
@media only screen and (min-width: 64em) {
  .testimonials-content {
    grid-template-columns: repeat(3, minmax(300px, 360px));
  }
}

.testimonial {
  font-size: 0.85em;
  padding: 1.5rem 1rem;
  background-color: var(--dark-test);
  border-radius: 8px;
  box-shadow: 4px 4px 10px -9px #fff;
}
@media only screen and (min-width: 64em) {
  .testimonial {
    font-size: 0.95em;
    padding: 2rem 1.5rem;
  }
}
.testimonial-text {
  line-height: 2;
  margin-bottom: 1rem;
}
.testimonial:first-child {
  position: inherit;
}
.testimonial:first-child::before {
  content: "";
  background: url(../../images/bg-quotes.png) no-repeat center;
  background-size: contain;
  position: absolute;
  top: -16%;
  left: 2%;
  width: 30px;
  height: 30px;
  z-index: -1;
}
@media only screen and (min-width: 64em) {
  .testimonial:first-child::before {
    width: 60px;
    height: 60px;
    top: -18%;
    left: -2%;
  }
}
.testimonial-author img {
  display: block;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  margin-right: 0.375rem;
}

/*Login*/
.login {
  text-align: center;
  position: relative;
  z-index: 1;
  max-width: 62.5rem;
  margin-left: auto;
  margin-right: auto;
}
.login-content {
  padding: 2.5625rem 1.5rem 2rem;
  background-color: var(--dark-intro);
  border-radius: 8px;
  box-shadow: 12px 12px 10px -6px black;
}
@media only screen and (min-width: 64em) {
  .login-content {
    padding: 3.5rem 4rem 2.5rem;
  }
}
.login-content > p {
  margin-bottom: 2rem;
  max-width: 43.75rem;
  margin-left: auto;
  margin-right: auto;
}
@media only screen and (min-width: 40em) {
  .login-inputs {
    flex-direction: row;
    gap: 1.5rem;
  }
}
.login-inputs input:first-child {
  margin-bottom: 1rem;
  padding-left: 1.5rem;
  font-weight: normal;
}
@media only screen and (min-width: 40em) {
  .login-inputs input:first-child {
    margin-bottom: 0;
    flex-basis: 80%;
  }
}
.login-inputs input:first-child::placeholder {
  color: rgba(0, 0, 0, 0.3);
}

.footer {
  background-color: var(--dark-footer);
  color: var(--clr-white);
  margin-top: -10.9375rem;
  padding-top: 15.625rem;
  padding-bottom: 2.75rem;
  font-family: var(--font-secondary);
}
@media only screen and (min-width: 64em) {
  .footer {
    padding-left: 7.5rem;
    padding-right: 7.5rem;
  }
}
.footer-logo {
  margin-bottom: 2rem;
  max-width: 90em;
  margin-left: auto;
  margin-right: auto;
}
.footer-content {
  display: grid;
  grid-template-columns: 1fr;
  max-width: 90em;
  margin-left: auto;
  margin-right: auto;
}
@media only screen and (min-width: 40em) {
  .footer-content {
    text-align: center;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media only screen and (min-width: 64em) {
  .footer-content {
    grid-template-columns: repeat(7, 1fr);
    align-items: flex-start;
    column-gap: 1rem;
  }
}
.footer-location {
  margin-bottom: 1.5rem;
}
@media only screen and (min-width: 64em) {
  .footer-location {
    grid-column: 1/3;
  }
}
.footer-location-dot {
  padding-left: 2rem;
}
@media only screen and (min-width: 64em) {
  .footer-location-dot {
    text-align: left;
  }
}
.footer-location-dot::before {
  content: url("../../images/icon-location.svg");
  margin-left: -2rem;
  margin-right: 1rem;
}
.footer-contact {
  margin-bottom: 4.6875rem;
}
@media only screen and (min-width: 64em) {
  .footer-contact {
    text-align: left;
    grid-column: 3/5;
    justify-self: center;
  }
}
.footer-contact-phone::before, .footer-contact-email::before {
  margin-right: 1rem;
}
.footer-contact-phone {
  margin-bottom: 1rem;
}
.footer-contact-phone::before {
  content: url("../../images/icon-phone.svg");
}
.footer-contact-email::before {
  content: url("../../images/icon-email.svg");
}
.footer-location, .footer-contact {
  font-size: 0.9375rem;
}
.footer-location > p, .footer-contact > p {
  line-height: 1.7;
}
.footer-about-us {
  margin-bottom: 3.125rem;
}
.footer-contact-us {
  margin-bottom: 4.6875rem;
}
.footer-about-us, .footer-contact-us {
  grid-row-gap: 1.25rem;
}
.footer-about-us > a, .footer-contact-us > a {
  display: inline-block;
  color: var(--clr-white);
}
.footer-about-us > a:hover, .footer-about-us > a:focus, .footer-contact-us > a:hover, .footer-contact-us > a:focus {
  font-weight: bold;
}
.footer-socialmedia {
  gap: 1rem;
}
@media only screen and (min-width: 40em) {
  .footer-socialmedia {
    grid-column: 1/-1;
  }
}
@media only screen and (min-width: 64em) {
  .footer-socialmedia {
    grid-column: initial;
  }
}
.footer-socialmedia > a {
  display: inline-flex;
  color: var(--clr-white);
  border-radius: 50%;
  border: 1px solid var(--clr-white);
  width: 30px;
  height: 30px;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  transition: color 200ms ease, border 200ms ease, background 200ms ease;
}
.footer-socialmedia > a > i {
  pointer-events: none;
}
.footer-socialmedia-fcb:hover {
  border: 1px solid #0777e7;
  background: #0777e7;
}
.footer-socialmedia-twt:hover {
  border: 1px solid #059aee;
  background: #059aee;
}
.footer-socialmedia-ins:hover {
  border: 1px solid purple;
  background: linear-gradient(0deg, yellow, red, purple);
}

.about-me {
  text-align: center;
  padding: 1.5rem;
  color: var(--clr-white);
  background-color: var(--dark-intro);
  font-size: 1.125rem;
}